import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import MediaCard from './MediaCard';

import ReactGA from 'react-ga';
ReactGA.initialize('UA-3409363-10', { testMode: process.env.NODE_ENV === 'test' });

function Projects(props) {
    useEffect(() => {
        // Update the document title using the browser API
        document.title = "DavidAdkins.com - Projects";
        ReactGA.pageview(window.location.pathname + window.location.search);
    });

    return (
        <Grid container justify="flex-start" alignItems="flex-start">
            <MediaCard title="AWS Deepracer" desc="See my attempt at an Amazon DeepRacer simulator. I trained on a variety of different tracks." />
            <MediaCard title="Self Driving RL RC Car" desc="I built an RC car with an Nvidia GPU on it. See my videos that show how it can follow a line." />
            <MediaCard title="Debate App" desc="My final project for CS 442, an allows people to side on a debate. Be kind, I would not consider it a secure system." />
            <MediaCard title="Digit Recognition" desc="My implementation of an ML model to learn digits.  Uses PyTorch and Captum to show importance of pixels." />
            <MediaCard title="Kaggle Competition" desc="My first Kaggle competition. I show how to determine who survives on the Titanic." />
            <MediaCard title="Arduino Indoor Camera" desc="I turn an Arduino into a security camera that sends text messages when it detects a person." />
        </Grid>
    );
}
export default Projects;